.bage {
  position: relative;
  height: auto;
  min-height: 140px;
  background: @colorPrimary2;
  display: flex;
  align-items: center;
  transition: @transMid;

  @media (max-width: @mobileLandscapeWBig) {
    min-height: 130px;
  }

  @media (max-width: @mobileLandscapeW) {
    min-height: 90px;
  }

  @media (max-width: @mobileW) {
    min-height: 70px;
  }

  .wrapper {
    // padding-left: 10px;
    // padding-right: 10px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  &__overlay {
    position: absolute;
    background: linear-gradient(
      to right,
      @colorPrimary,
      rgba(@colorPrimary, 0.1)
    );
    clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
    width: 55%;
    height: 100%;
    z-index: 1;
    left: -40px;
    top: 0;
    transition: @transMid;
  }
  .title,
  .subtitle {
    position: relative;
    color: #fff;
    z-index: 1;
  }

  .subtitle {
    font-size: @font-15;
    line-height: @font-20;
    font-weight: 600;
  }

  .title {
    font-size: @font-50;
    line-height: @font-60;
    margin-bottom: 0;
  }

  &__canvas {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
}

@media (max-width: @wrapperMaxWidthAdjusted) {
  .bage {
    .wrapper {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

@media (max-width: @mobileLandscapeWBig) {
  .bage {
    // min-height: 120px;

    .subtitle {
      font-size: @font-14;
    }

    .title {
      font-size: @font-40;
      line-height: @font-50;
    }
  }
}

@media (max-width: @mobileLandscapeW) {
  .bage {
    // min-height: 100px;
    .subtitle {
      font-size: @font-13;
    }

    .title {
      font-size: @font-30;
      line-height: @font-40;
    }
    .wrapper {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }
}

@media (max-width: @mobileW) {
  .bage {
    // min-height: 90px;
    .subtitle {
      font-size: @font-14;
    }

    .title {
      font-size: @font-25;
      line-height: @font-30;
    }

    .wrapper {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }
}

html.color-white,
html.color-black {
  .bage {
    background: @blackEye;
    &__overlay {
      background: linear-gradient(to right, @blackEye, rgba(@blackEye, 0.1));
    }
    .title,
    .subtitle {
      color: @whiteEye;
    }
  }
}

html.color-black {
  .bage {
    .border-section-like_white();
  }
}

_:-ms-fullscreen,
:root .bage {
  .bage__overlay {
    transform: skewX(-26deg);
  }
}
